export default {
	data() {
		return {
			username:null,
			password:null,
			isShow:true,
			btnShow:true,
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500.,
			scrollTop: 0,
			old: {
				scrollTop: 0
			},
			focusPlaylist: [],
			hotPlaylist: []
		};
	},
	onLoad() {
		uni.request({
			url: '../../static/data/focus.json',
			method: 'GET',
			data: {},
			success: (res) => {
				// console.log(res.data.data)
				this.focusPlaylist = res.data.data
				// console.log(this.focusPlaylist)
			},
			fail: (err) => "网络连接超时",
			complete: () => {}
		})
		uni.request({
			url: '../../static/data/recommend.json',
			method: 'GET',
			data: {},
			success: (res) => {
				this.hotPlaylist = res.data.data
			},
			fail: (err) => "网络连接超时",
			complete: () => {}
		})			
	},
	methods:{
		getUsernameValue(e) {
			this.username = e.detail.value
		},
		getPasswordValue(e) {
			this.password = e.detail.value
		},
		loading() {
			this.username && this.password ? this.isShow = true : this.isShow = false;	
		},
		handleExit() {
			this.username = null
			this.password = null
			this.btnShow = false
		},
		toloading() {
			if (!this.username) {
				this.isShow = false
			}				
			this.btnShow = true
		},
		toPlayerPage(e){
			console.log(e.currentTarget.dataset.focusid)
			let id = e.currentTarget.dataset.focusid
			uni.navigateTo({
				url: '../focus-course/focus-course?focusid=' + id
			})
		},
		scroll(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
	}
}
